import BaseModal from '@base/components/baseModal';
import { Sprite, Texture } from 'pixi.js';
import { ImageKey, GameEvent, VIEW_LIST, GAME_STATE, SoundKey } from '../../constants';
import Button from '@base/components/button';
import { EventManager, EventMessage  } from '@base/tool/eventManager';
import Audio from '@base/tool/audio';
import PlayerPrefs from '@base/tool/playerPrefs';

export default class pausePopModal extends BaseModal {
    private bgSprite: Sprite;
    private toLobbyBtn: Button;
    private resumeBtn: Button;
    private audioBtn: Button;

    constructor() {
        super()
        this.initContent();
    }

    public initContent(): void {
        this.bgSprite = new Sprite(Texture.from(ImageKey.pause_panel));
        this.bgSprite.position.set(- this.bgSprite.width / 2 , - this.bgSprite.height / 2);
        this.contentContainer.addChild(this.bgSprite);

        this.toLobbyBtn = new Button(ImageKey.to_lobby_btn);
        this.toLobbyBtn.onTap = () => {
            this.closeWithAnim();
            EventManager.SendMessage(GameEvent.CHANGE_VIEW, new EventMessage(VIEW_LIST.HOME));
            Audio.playSound(SoundKey.lwhsg_click_button);
        }
        this.toLobbyBtn.position.set(- this.bgSprite.width / 2 * 0.5, this.bgSprite.height / 2 * 0.1);
        this.contentContainer.addChild(this.toLobbyBtn);

        this.resumeBtn = new Button(ImageKey.resume_btn);
        this.resumeBtn.onTap = () => {
            this.closeWithAnim();
            EventManager.SendMessage(GameEvent.CHANGE_GAME_STATE, new EventMessage(GAME_STATE.GAME_START));
            Audio.playSound(SoundKey.lwhsg_click_button);
        }
        this.resumeBtn.position.set(this.bgSprite.width / 2 * 0.1, this.bgSprite.height / 2 * 0.1);
        this.contentContainer.addChild(this.resumeBtn);

        this.audioBtn = new Button(ImageKey.audio_on_btn);
        this.audioBtn.onTap = () => {
            Audio.playSound(SoundKey.lwhsg_click_button);
            let clickAudio = PlayerPrefs.Get("lwhsg_music");
            this.changeAudio(clickAudio);
        }
        this.audioBtn.position.set(this.bgSprite.width / 2 * 0.6, this.bgSprite.height / 2 * 0.1);
        this.contentContainer.addChild(this.audioBtn);

        this.initAudio();
    }

    public reset(): void {
    }

    private initAudio(): void {
        if(PlayerPrefs.Get('lwhsg_music')) {
            this.audioBtn.texture = Texture.from(ImageKey.audio_on_btn);
            Audio.playMusic(SoundKey.lwhsg_game_bgm);
            Audio.setVolume(1);
        } else {
            this.audioBtn.texture = Texture.from(ImageKey.audio_off_btn);
            Audio.stopMusic();
            Audio.setVolume(0);
        }
    }

    private changeAudio(clickAudio: boolean): void {
        if(clickAudio) {
            this.audioBtn.texture = Texture.from(ImageKey.audio_off_btn);
            PlayerPrefs.Set('lwhsg_music', false);
            Audio.stopMusic();
            Audio.setVolume(0);
        } else {
            this.audioBtn.texture = Texture.from(ImageKey.audio_on_btn);
            PlayerPrefs.Set('lwhsg_music', true);
            Audio.playMusic(SoundKey.lwhsg_game_bgm);
            Audio.setVolume(1);
        }
    }
}