import BaseModal from '@base/components/baseModal';
import { Sprite, Texture, Text, Container } from 'pixi.js';
import { ImageKey, SoundKey } from '../../constants';
import GlobalGameData from '../../model/globalGameData';
import Button from '@base/components/button';
import ScrollView, { SCROLL_DIRECTION } from '@base/components/scrollView';
import Method from '@base/tool/method';
import PlayerPrefs from '@base/tool/playerPrefs';
import Audio from '@base/tool/audio';

enum PAGE {
    ILLUSTRATE = 0,
    RENAME,
    RANK
}

const terms_text = "本公司在此提醒此遊戲的使用者";

export default class RankPopModal extends BaseModal {
    private bgSprite: Sprite;
    private termsText: Text;
    private agreementLabel: Sprite;
    private disagreeBtn: Button;
    private agreeBtn: Button;
    private viewArray: Array<any> = new Array();
    private comfirm_btn: Button;
    private textfield_panel: Sprite;
    private random_btn: Button;
    private rename_text: Text;
    private rank_title: Sprite;
    private rank_attributebar: Sprite;
    private rank_text: Text;
    private player_text: Text;
    private score_text: Text;
    private scrollView: ScrollView;
    private scrollViewContainer: Container;
    private user_context_bg: Sprite;
    private user_name: Text;
    private user_score: Text;
    private closeBtn: Button;
    private changeNameBtn: Button;

    constructor() {
        super()
        this.initContent();
    }

    public initContent(): void {
        this.bgSprite = new Sprite(Texture.from(ImageKey.agreement_bg));
        this.contentContainer.addChild(this.bgSprite);

        this.initTerms();
        this.initRename();
        this.initRank();
        this.resetView();
    }

    public reset(): void {
        if(GlobalGameData.Agree) {
            this.changePage(PAGE.RENAME);
        } else {
            this.changePage(PAGE.ILLUSTRATE);
        }
        this.resetView();
        let userInfo = PlayerPrefs.Get('LWHSG_USER_INFO');
        this.user_score.text = userInfo?.score ? userInfo.score : 0;
        this.rename_text.text = userInfo?.name ? userInfo.name : this.rename_text.text;
    }

    private initTerms(): void {
        let arr = new Array();
        this.termsText = new Text(terms_text);
        this.contentContainer.addChild(this.termsText);

        this.agreementLabel = new Sprite(Texture.from(ImageKey.aggrement_label));
        this.contentContainer.addChild(this.agreementLabel);

        this.disagreeBtn = new Button(ImageKey.disagree_btn);
        this.disagreeBtn.onTap = () => {
            Audio.playSound(SoundKey.lwhsg_click_button);
            this.closeWithAnim();
            GlobalGameData.Agree = false;
        }
        this.contentContainer.addChild(this.disagreeBtn);

        this.agreeBtn = new Button(ImageKey.agree_btn);
        this.agreeBtn.onTap = () => {
            Audio.playSound(SoundKey.lwhsg_click_button);
            this.changePage(PAGE.RENAME);
            GlobalGameData.Agree = true;
        }
        arr.push(this.termsText, this.agreementLabel, this.disagreeBtn, this.agreeBtn);
        this.viewArray.push(arr);
        this.contentContainer.addChild(this.agreeBtn);
    }

    private initRename(): void {
        let arr = new Array();
        this.comfirm_btn = new Button(ImageKey.comfirm_btn);
        this.comfirm_btn.onTap = () => {
            this.changePage(PAGE.RANK);
            this.user_name.text = this.rename_text.text;
            Audio.playSound(SoundKey.lwhsg_click_button);
        }
        this.contentContainer.addChild(this.comfirm_btn);

        this.textfield_panel = new Sprite(Texture.from(ImageKey.textfield_panel));
        this.contentContainer.addChild(this.textfield_panel);
        
        this.random_btn = new Button(ImageKey.random_btn);
        this.random_btn.onTap = () => {
            this.changeName();
            Audio.playSound(SoundKey.lwhsg_click_button);
        }
        this.contentContainer.addChild(this.random_btn);

        this.rename_text = new Text("player");
        this.contentContainer.addChild(this.rename_text);

        arr.push(this.comfirm_btn, this.textfield_panel, this.random_btn, this.rename_text);
        this.viewArray.push(arr);

    }

    private initRank(): void {
        let arr = new Array();

        this.scrollViewContainer = new Container();
        let score = 2000;
        for(let i = 1; i < 20; i++) {
            this.createRankItem(i, score);
            score--;
        }
  
        this.scrollView = new ScrollView(600, 370, this.scrollViewContainer, this.scrollViewContainer.width, this.scrollViewContainer.height, SCROLL_DIRECTION.VERTICAL);
        this.scrollView.ContainerExceedRatio = 0.1;
        this.contentContainer.addChild(this.scrollView);

        this.rank_title = new Sprite(Texture.from(ImageKey.rank_title));
        this.contentContainer.addChild(this.rank_title);

        this.rank_attributebar = new Sprite(Texture.from(ImageKey.rank_attributebar));
        this.contentContainer.addChild(this.rank_attributebar);

        this.user_context_bg = new Sprite(Texture.from(ImageKey.rank_context));
        this.contentContainer.addChild(this.user_context_bg);

        this.rank_text = new Text('排名');
        this.contentContainer.addChild(this.rank_text);
        this.player_text = new Text('玩家');
        this.contentContainer.addChild(this.player_text);
        this.score_text = new Text('分數');
        this.contentContainer.addChild(this.score_text);

        this.user_name = new Text('user');
        this.contentContainer.addChild(this.user_name);

        this.user_score = new Text('0');
        this.contentContainer.addChild(this.user_score);

        this.closeBtn = new Button(ImageKey.close_btn);
        this.closeBtn.onTap = () => {
            this.closeWithAnim();
            Audio.playSound(SoundKey.lwhsg_click_button);
        }
        this.contentContainer.addChild(this.closeBtn);

        this.changeNameBtn = new Button(ImageKey.rename_btn);
        this.changeNameBtn.onTap = () => {
            Audio.playSound(SoundKey.lwhsg_click_button);
            this.changePage(PAGE.RENAME);
        }
        this.contentContainer.addChild(this.changeNameBtn);

        arr.push(this.rank_title, this.rank_attributebar, this.user_context_bg, this.rank_text, this.player_text, this.score_text, this.scrollView, this.user_name, this.user_score, this.closeBtn, this.changeNameBtn);
        this.viewArray.push(arr);
    }

    private changePage(page: PAGE): void {
        switch(page) {
            case PAGE.ILLUSTRATE:
                this.bgSprite.texture = Texture.from(ImageKey.agreement_bg);
                break;
            case PAGE.RENAME:
                this.bgSprite.texture = Texture.from(ImageKey.rename_panel);
                break;
            case PAGE.RANK:
                this.bgSprite.texture = Texture.from(ImageKey.rank_context_bg);
                this.user_score.text = GlobalGameData.BestScore.toString();
                break;
            
        }
        for(let i = 0; i < this.viewArray.length; i++) {
            if(i == page) {
                this.viewArray[i].forEach((item: any) => {
                    item.visible = true;
                })
            }else{
                this.viewArray[i].forEach((item: any) => {
                    item.visible = false;
                })
            }
        }
        this.resetView();
    }

    private resetView(): void {
        this.bgSprite.position.set(- this.bgSprite.width / 2 , - this.bgSprite.height / 2);
        this.termsText.position.set(-this.termsText.width / 2, -this.termsText.height /2);
        this.agreementLabel.position.set(- this.agreementLabel.width / 2 , - this.agreementLabel.height * 2.8);
        this.disagreeBtn.position.set(- this.disagreeBtn.width, this.bgSprite.height / 2 - this.disagreeBtn.height);
        this.agreeBtn.position.set(this.agreeBtn.width, this.bgSprite.height / 2 - this.agreeBtn.height);
        this.comfirm_btn.position.set(0, this.bgSprite.height / 2 * 0.55);
        this.textfield_panel.position.set(-this.textfield_panel.width / 2, 0);
        this.random_btn.position.set(this.textfield_panel.width / 2, this.textfield_panel.height / 2);
        this.rename_text.position.set(- this.textfield_panel.width / 2 * 0.9, this.textfield_panel.height / 2 * 0.5);
        this.rank_title.position.set(-this.rank_title.width / 2, -this.bgSprite.height / 2 * 1.2);
        this.rank_attributebar.position.set(-this.rank_attributebar.width / 2, -this.bgSprite.height / 2 * 0.8);
        this.user_context_bg.position.set(-this.user_context_bg.width / 2, this.bgSprite.height / 2 * 0.64);
        this.rank_text.position.set(this.rank_attributebar.position.x * 0.8, this.rank_attributebar.position.y  * 0.95);
        this.player_text.position.set(this.rank_attributebar.position.x * 0.1, this.rank_attributebar.position.y  * 0.95);
        this.score_text.position.set(-this.rank_attributebar.position.x * 0.7, this.rank_attributebar.position.y  * 0.95);
        this.user_name.position.set(this.user_context_bg.position.x * 0.1, this.user_context_bg.position.y  * 1.05);
        this.user_score.position.set(-this.user_context_bg.position.x * 0.79, this.user_context_bg.position.y  * 1.05);
        this.scrollView.position.set(-this.scrollViewContainer.width / 2, -this.bgSprite.height / 2 * 0.8);
        this.closeBtn.position.set(this.bgSprite.width / 2 * 0.7, - this.bgSprite.height / 2);
        this.changeNameBtn.position.set( this.user_context_bg.position.x * 0.25, this.user_context_bg.position.y * 1.15);
    }

    private createRankItem(rank: number, score: number): void {
        let itemContainer = new Container();
        let bg = new Sprite(Texture.from(ImageKey.rank_paihang));
        itemContainer.addChild(bg);

        let rank_text = new Text(rank.toString());
        rank_text.position.set(bg.width / 2 * 0.25, bg.height / 2 * 0.4);
        itemContainer.addChild(rank_text);
        let player_text = new Text(`pleyer${rank}`);
        player_text.position.set(bg.width / 2 * 0.86, bg.height / 2 * 0.4);
        itemContainer.addChild(player_text);
        let score_text = new Text(score.toString());
        score_text.position.set(bg.width / 2 * 1.69, bg.height / 2 * 0.4);
        itemContainer.addChild(score_text);
        itemContainer.position.set(0, itemContainer.height * rank * 1.1);
        this.scrollViewContainer.addChild(itemContainer);
    }

    private changeName(): void {
        let text = '';
        let possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
        let randomLength = Method.RandomIntRange(3 , 6);
        for (var i = 0; i < randomLength; i++) {
            text += possible.charAt(Math.floor(Math.random() * possible.length));
        }
        this.rename_text.text = text;
        PlayerPrefs.Set('LWHSG_USER_INFO',{
            name: text
        });
    }
}