export default class GlobalListenerAdapter {
    private globalListener: any;

    // 存取lobby丟過來的globalListener
    public setGlobalListener(globalListener: any) {
        this.globalListener = globalListener;
    }

    // 註冊visible事件
    public registerVisibleEvent(visibleEvent: any) {
        if (!this.globalListener) {
            console.error('globalListener undefined');
            return;
        }
        this.globalListener.registerVisibleEvent(visibleEvent);
    }

    // 註冊popState事件
    public registerPopStateEvent(popStateEvent: any) {
        if (!this.globalListener) {
            console.error('globalListener undefined');
            return;
        }
        this.globalListener.registerPopStateEvent(popStateEvent);
    }

    // 註冊focus事件
    public registerFocusEvent(focusEvent: any) {
        if (!this.globalListener) {
            console.error('globalListener undefined');
            return;
        }
        this.globalListener.registerFocusEvent(focusEvent);
    }

    // 註冊blur事件
    public registerBlurEvent(blurEvent: any) {
        if (!this.globalListener) {
            console.error('globalListener undefined');
            return;
        }
        this.globalListener.registerBlurEvent(blurEvent);
    }
}