import BaseModal from '@base/components/baseModal';
import { Sprite, Texture } from 'pixi.js';
import { ImageKey, MAX_SIZE, GAME_TIME_STATE } from '../../constants';

export default class StateViewPopModal extends BaseModal {
    private topBgSprite: Sprite;
    private bottomBgSprite: Sprite;

    constructor() {
        super()
        this.initContent();
    }

    public initContent(): void {
        this.topBgSprite = new Sprite(Texture.from(ImageKey.open_img));
        this.topBgSprite.position.set(- this.topBgSprite.width / 2, - MAX_SIZE.height / 2 * 0.9);
        this.contentContainer.addChild(this.topBgSprite);

        this.bottomBgSprite = new Sprite(Texture.from(ImageKey.activity_img_4));
        this.bottomBgSprite.position.set(- this.bottomBgSprite.width / 2, MAX_SIZE.height / 2 * 0.3);
        this.contentContainer.addChild(this.bottomBgSprite);
    }

    public reset(): void {
    }

    public changeImage(time: GAME_TIME_STATE): void {
        switch(time) {
            case GAME_TIME_STATE.OPEN:
                this.topBgSprite.texture = Texture.from(ImageKey.open_img);
                this.bottomBgSprite.texture = Texture.from(ImageKey.activity_img_4);
                this.topBgSprite.position.y = - MAX_SIZE.height / 2 * 0.9;
                this.bottomBgSprite.visible = true;
                break;
            case GAME_TIME_STATE.REST:
                this.topBgSprite.texture = Texture.from(ImageKey.rest_img);
                this.topBgSprite.position.y = - MAX_SIZE.height / 4;
                this.bottomBgSprite.visible = false;
                break;
            case GAME_TIME_STATE.CLOSE:
                this.topBgSprite.texture = Texture.from(ImageKey.close_img);
                this.topBgSprite.position.y = - MAX_SIZE.height / 4;
                this.bottomBgSprite.visible = false;
                break;
        }
    }
}