import { Sprite } from 'pixi.js';
import RectangleCollider2D from '@base/collider/rectangleCollider2d';
import ICollider from '@base/collider/ICollider';
import Collider2DBase from '@base/collider/collider2dBase';
import ColliderManager from '@base/collider/colliderManager';
import Mixture from '../mixture';
import { GameEvent } from '../../constants';
import { EventManager, EventMessage  } from '@base/tool/eventManager';

export default class GuestTableCollider extends Sprite implements ICollider {
    private rectangleCollider2D: RectangleCollider2D;
    private mixture: Mixture;

    constructor(tag: number) {
        super();
        this.rectangleCollider2D = new RectangleCollider2D('GuestTableCollider', 20, 20, `GuestTableCollider${tag}`);
        ColliderManager.Ins.AddCollider(this.rectangleCollider2D);
        this.rectangleCollider2D.Init(this);

        this.rectangleCollider2D.position.set(30, 30);
        this.rectangleCollider2D.pivot.set(0);
        this.addChild(this.rectangleCollider2D);

        this.mixture = new Mixture();
        this.mixture.pivot.set(0.5);
        this.mixture.position.set(-10, 0);
        this.addChild(this.mixture);
        EventManager.AddListener(GameEvent.RESET_POT, (msg: any) => {this.resetPot()});
    }

    /** 在引擎更新後觸發，開始碰撞的物體 */
    public CollisionStart(colliderSender: Collider2DBase, colliderHandler: Collider2DBase): void {
    }

    /** 在引擎更新後觸發，持續發生碰撞的物體 */
    public CollisionActive(colliderSender: Collider2DBase, colliderHandler: Collider2DBase): void {

    }

    /** 在引擎更新後觸發，離開碰撞的物體 */
    public CollisionEnd(colliderSender: Collider2DBase, colliderHandler: Collider2DBase): void {
        let customData = colliderSender.CustomData;
        if(colliderSender.Tag === "StoveCollider" && customData) {
            if(colliderHandler.Tag === `GuestTableCollider${customData.animalRoute}`) {
                this.mixture.resetView();
                this.mixture.playＷholeObjectAni(customData.data);
                EventManager.SendMessage(GameEvent.SET_MENU, new EventMessage(customData.data));

            }
        }
    }

    public resetPot(): void {
        this.mixture.resetView();
    }
}