import { EventManager } from '@base/tool/eventManager';
import { GameEvent, GAME_STATE, VIEW_LIST } from '../constants';
import StateMachine from '../fsm/stateMachine';
import RootView from '../view/rootView';
import TimeManager from '@base/tool/timeManager';
import ColliderManager from '@base/collider/colliderManager';
import ScreenManager, { SUBSCRIBE_TYPE } from '@base/tool/screenManager';

// 遊戲邏輯控管
export default class GameController {
    private stateMachine: StateMachine;
    private rootView: RootView;

    constructor() {
        this.registerEvent();
        this.stateMachine
    }

    /**
     * 註冊事件
     */
    private registerEvent(): void {
        EventManager.AddListener(GameEvent.CHANGE_VIEW, (msg) => { this.onChangeView(msg); });
        EventManager.AddListener(GameEvent.CHANGE_GAME_STATE, (msg) => { this.onGameStateChange(msg); });
    }

    public setRootView(rootView: RootView): void {
        this.rootView = rootView;
    }

    public initStateMachine(): void {
        this.stateMachine = new StateMachine();
        this.stateMachine.initState(this.rootView);
        this.stateMachine.changeState(GAME_STATE.GAME_STOP);
    }

    /**
     * 切換場景
     * @param msg 
     */
    public onChangeView(msg: any): void {
        let viewIndex: VIEW_LIST = msg.cMsg[0];
        this.rootView.changeView(viewIndex);
    }

    public onGameStateChange(msg: any): void {
        let gameState: GAME_STATE = msg.cMsg[0];
        let data: any = msg.cMsg[1];
        this.stateMachine.changeState(gameState, data);
    }

    // 返回大廳時呼叫clearAll清除所有註冊物件
    public clearAll(): void {
        ScreenManager.unSubscribeAll(SUBSCRIBE_TYPE.GAME);
        TimeManager.removeGameHandlerList();
        ColliderManager.Ins.Clear();
        this.stateMachine.destroy();
    }
}