import { Container, IPoint, IPointData } from 'pixi.js';
import { INGREDIENTS_TAG, GameEvent, ANIMAL_ROUTE } from '../../constants';
import RectangleCollider2D from '@base/collider/rectangleCollider2d';
import ICollider from '@base/collider/ICollider';
import Collider2DBase from '@base/collider/collider2dBase';
import ColliderManager from '@base/collider/colliderManager';
import Mixture from '../mixture';
import ScreenManager from '@base/tool/screenManager';
import { EventManager } from '@base/tool/eventManager';

export default class StoveCollider extends Container implements ICollider {
    private rectangleCollider2D: RectangleCollider2D;
    private mixture: Mixture;
    private havePot: boolean = false;
    private checklist: Array<string>;
    private playAni: boolean = false;
    private canMove: boolean = false;
    private centerContainer: Container;
    private originalPos: IPointData;
    private animalRoute: ANIMAL_ROUTE;

    constructor() {
        super();
        this.rectangleCollider2D = new RectangleCollider2D('StoveCollider', 20, 20, "StoveCollider");
        ColliderManager.Ins.AddCollider(this.rectangleCollider2D);
        this.rectangleCollider2D.Init(this);
        this.addChild(this.rectangleCollider2D);

        this.mixture = new Mixture();
        this.mixture.pivot.set(0.5);
        this.mixture.position.set(-55, -40);
        this.addChild(this.mixture);

        this.checklist = [];
        
        this.on('pointerdown', (e) => {
            this.originalPos = {
                x: this.position.x,
                y: this.position.y,
            };
            if(this.interactive) {
                this.canMove = true;
            } else {
                this.canMove = false;
            }
            this.moveHandler(e.data.global);
        });
        this.on('pointermove', (e) => {
            if(!this.canMove) return;
            this.moveHandler(e.data.global);
        });
        this.on('pointerup', (e) => {
            if(this.canMove) {
                this.position.set(this.originalPos.x, this.originalPos.y);
            }
            this.canMove = false;
        });

        EventManager.AddListener(GameEvent.ANIMAL_DETAIL, (msg: any) => { this.checkAnimalDetail(msg) });
        EventManager.AddListener(GameEvent.RESET_MENU, (msg: any) => { this.resetMenu() });
        EventManager.AddListener(GameEvent.STOVE_PLAYING, (msg: any) => { this.setPlayAni(msg) });
        EventManager.AddListener(GameEvent.RESET_STOVE_VIEW, (msg: any) => {this.resetPot()});
    }

    public resetView(): void {
        this.mixture.resetView();
        if(this.originalPos) {
            this.position.set(this.originalPos.x, this.originalPos.y);
        }
        this.interactive = false;
        this.havePot = false;
        this.canMove = false;
        this.playAni = false;
        this.checklist = [];
    }

    private resetMenu(): void {
        this.rectangleCollider2D.CustomData = null;
        this.animalRoute = null;
    }

    /** 在引擎更新後觸發，開始碰撞的物體 */
    public CollisionStart(colliderSender: Collider2DBase, colliderHandler: Collider2DBase): void {
        this.mixture.interactive = false;
        if(colliderSender.Tag === `GuestTableCollider${this.animalRoute}`) {
            this.rectangleCollider2D.CustomData = {
                data: this.mixture.getWholeData(),
                animalRoute: this.animalRoute
            }
            this.resetView();
        }
    }

    /** 在引擎更新後觸發，持續發生碰撞的物體 */
    public CollisionActive(colliderSender: Collider2DBase, colliderHandler: Collider2DBase): void {
    }

    /** 在引擎更新後觸發，離開碰撞的物體 */
    public CollisionEnd(colliderSender: Collider2DBase, colliderHandler: Collider2DBase): void {
        if(!this.playAni) return;

        let nullValue = this.checklist.indexOf(colliderSender.Tag) === -1;
        let callBack = () => {
            this.playAni = false;
            this.mixture.interactive = true;
        };
        if(!this.havePot) {
            if(colliderSender.Tag === INGREDIENTS_TAG[0]) {
                this.interactive = true;
                this.havePot = true;
                this.mixture.playAni(colliderSender.Tag, callBack);
            } else {
                this.havePot = false;
                this.interactive = false;
            }
        }else {
            this.interactive = true;
            if(nullValue && colliderSender.Tag !== INGREDIENTS_TAG[0]) {
                this.mixture.playAni(colliderSender.Tag, callBack);
                this.checklist.push(colliderSender.Tag);
            }
        }
    }

    public setPlayAni(msg: any): void {
        this.playAni = msg.cMsg[0];
    }

    public playSmokeAni(): void {

    }

    public setCenterContainer(centerContainer: Container): void {
        this.centerContainer = centerContainer;
    }

    public checkAnimalDetail(msg: any): void {
        this.animalRoute = msg.cMsg[0].animalRoute;
    }

    public resetPot(): void {
        this.resetView();
    }

    private moveHandler(pos: IPoint): void {
        let posX = pos.x / ScreenManager.gameRatio   - this.centerContainer.position.x
        let posY = pos.y / ScreenManager.gameRatio - this.centerContainer.position.y;
        this.position.set(posX, posY);
    }
}