import PlayerPrefs from '@base/tool/playerPrefs';

// 使用get set的方式來取得資料
export default class GlobalGameData {
    private static score: number = 0;

    public static get Score(): number {
        return this.score;
    }

    public static set Score(score: number) {
        this.score = score;
    }

    public static get BestScore(): number {
        if (PlayerPrefs.Get('LWHSG_BEST_SCORE') === undefined) {
            GlobalGameData.BestScore = 0;
            return 0;
        }
        return PlayerPrefs.Get('LWHSG_BEST_SCORE');
    }

    public static set BestScore(score: number) {
        if(PlayerPrefs.Get('LWHSG_BEST_SCORE') < score || !PlayerPrefs.Get('LWHSG_BEST_SCORE')) {
            PlayerPrefs.Set('LWHSG_BEST_SCORE', score);
        }
    }

    public static get Agree(): boolean {
        if (PlayerPrefs.Get('LWHSG_Agree') === undefined) {
            GlobalGameData.Agree = false;
            return false;
        }
        return PlayerPrefs.Get('LWHSG_Agree');
    }

    public static set Agree(bool: boolean) {
        PlayerPrefs.Set('LWHSG_Agree', bool);
    }
}