import { Container } from 'pixi.js';
import { MAX_SIZE } from '../constants';

export default abstract class BaseView extends Container {
    /**
     * 置中物件層級
     */
    public centerContainer: Container;
    public otherContainer: Container;
    constructor() {
        super();
        this.otherContainer = new Container();
        this.centerContainer = new Container();
        this.addChild(this.otherContainer);
        this.addChild(this.centerContainer);
        this.init();
    }

    /**
     * 初始化呼叫
     */
    public abstract init(): void;

    /**
     * 顯示場景
     */
    public onViewShow(): void {
        this.visible = true;
    }

    /**
     * 關閉場景
     */
    public onViewHide(): void {
        this.visible = false;
        this.resetView();
    }

    /**
     * 繼承使用, 無置中物件Resize時需自行對位
     */
    public onResize(width: number, height: number): void {
        this.centerContainer.position.set((width - MAX_SIZE.width) / 2, (height - MAX_SIZE.height) / 2);
    }

    /**
     * 重置場景
     */
    public abstract resetView(): void;

    /**
     * 由上層調用視窗是否可見
     * @param isVisible
     */
    public abstract onVisibilityChanged(isVisible: boolean): void;

    /**
     * Destroy事件
     */
    public destroy(): void {
        this.resetView();
        super.destroy({ children: true });
        Object.keys(this).forEach(key => this[key] = null);
    }
}