import BaseModal from '@base/components/baseModal';
import { Sprite, Texture, Text } from 'pixi.js';
import { ImageKey, MAX_SIZE, GameEvent, GAME_STATE, VIEW_LIST, SoundKey } from '../../constants';
import Button from '@base/components/button';
import { EventManager, EventMessage  } from '@base/tool/eventManager';
import Audio from '@base/tool/audio';

export default class ResultPopModal extends BaseModal {
    private bgSprite: Sprite;
    private retryBtn: Button;
    private toLobbyBtn: Button;
    private dayText: Text;
    private score: Text;
    private bestScore: Text;

    constructor() {
        super()
        this.initContent();
    }

    public initContent(): void {
        this.bgSprite = new Sprite(Texture.from(ImageKey.result_panel));
        this.bgSprite.position.set(- this.bgSprite.width / 2, - MAX_SIZE.height / 2 * 0.7);
        this.contentContainer.addChild(this.bgSprite);

        this.retryBtn = new Button(ImageKey.retry_btn);
        this.retryBtn.position.set(this.bgSprite.width * 0.3, this.bgSprite.height * 0.27);
        this.retryBtn.onTap = () => {
            EventManager.SendMessage(GameEvent.CHANGE_GAME_STATE, new EventMessage(GAME_STATE.GAME_START));
            this.closeWithAnim();
            Audio.playSound(SoundKey.lwhsg_click_button);
        }
        this.contentContainer.addChild(this.retryBtn);

        this.toLobbyBtn = new Button(ImageKey.to_lobby_btn);
        this.toLobbyBtn.position.set(-this.bgSprite.width * 0.3, this.bgSprite.height * 0.27);
        this.toLobbyBtn.onTap = () => {
            EventManager.SendMessage(GameEvent.CHANGE_VIEW, new EventMessage(VIEW_LIST.HOME));
            this.closeWithAnim();
            Audio.playSound(SoundKey.lwhsg_click_button);
        }
        this.contentContainer.addChild(this.toLobbyBtn);

        this.dayText = new Text('1');
        this.dayText.position.set(50, -115);
        this.contentContainer.addChild(this.dayText);
        this.score = new Text('1');
        this.score.position.set(50, -60);
        this.contentContainer.addChild(this.score);
        this.bestScore = new Text('1');
        this.bestScore.position.set(50, -5);
        this.contentContainer.addChild(this.bestScore);
    }

    public setDayText(text: any): void {
        this.dayText.text = text;
    }

    public setScore(text: any): void {
        this.score.text = text;
    }

    public setBestScore(text: any): void {
        this.bestScore.text = text;
    }

    public reset(): void {
    }
}