import { Sprite, Texture, Container, NineSlicePlane } from 'pixi.js';
import { ImageKey } from '../constants';

export default class Countdown extends Container {
    private bgSprite: Sprite;
    private clock_bg: Sprite;
    private playAni: boolean = false;
    private percetageBg: NineSlicePlane;
    private percetageBgWidth: number;

    constructor() {
        super()
        this.init();
        this.scale.set(0.8);
    }

    public init(): void {
        this.bgSprite = new Sprite(Texture.from(ImageKey.bar_bg));
        this.addChild(this.bgSprite);
        this.percetageBg = new NineSlicePlane(
            Texture.from(ImageKey.time_bar)
        );
        this.percetageBg.position.set(52, 12);
        this.addChild(this.percetageBg);
        this.clock_bg = new Sprite(Texture.from(ImageKey.clock_bg));
        this.clock_bg.position.set(0, -15);
        this.addChild(this.clock_bg);
        this.percetageBgWidth = this.percetageBg.width - 40;
    }

    public playCountdownAni (time: number): void {
        if(this.playAni) {
            let percetageTime = time / 60 * 100;
            this.percetageBg.width = Math.floor(this.percetageBgWidth * percetageTime / 100);
        }
    }

    public setplayAni(bool: boolean): void {
        this.playAni = bool;
    }

    public reset(): void {
        this.percetageBg.width = this.percetageBgWidth;
    }
}