import State from './state';

export default class GameResumeState extends State {
    public enterState(data?: any): void {
        this.gameView.getAnimal().pauseAnimalAnimation();
        this.gameView.getAnimal().setPauseAnimal(false);
        this.gameView.getPausePopModal().openWithAnim();
    }

    public leaveState(): void {
        this.gameView.getAnimal().setPauseAnimal(true);
    }

    public clearAll(): void {

    }
}