import { Container, Sprite, Texture } from 'pixi.js';
import GameView from './gameView';
import HomeView from './homeView';
import ScreenManager from '@base/tool/screenManager';
import BaseView from './baseView';
import { ImageKey, VIEW_LIST, GameEvent, MAX_SIZE, GAME_STATE, GAME_TIME_STATE } from '../constants';
import { EventManager, EventMessage  } from '@base/tool/eventManager';
import PlayerPrefs from '@base/tool/playerPrefs';

export default class RootView extends Container {
    private homeView: HomeView;
    private gameView: GameView;
    private currentView: BaseView;
    private bg: Sprite;

    constructor() {
        super();
        this.handleResize = this.handleResize.bind(this);

        this.init();
    }

    private init(): void {
        this.bg = new Sprite(Texture.from(ImageKey.LWHSG_lobby_bg));
        this.addChild(this.bg);

        this.homeView = new HomeView();
        this.addChild(this.homeView);
        this.homeView.onViewShow();

        this.gameView = new GameView();
        this.addChild(this.gameView);
        this.gameView.onViewHide();

        this.currentView = this.homeView;

        ScreenManager.subscribe(this.handleResize);
        this.handleResize();
        this.getAudio();
    }

    public changeView(viewIndex: VIEW_LIST): void {
        this.currentView.onViewHide();
        switch (viewIndex) {
            case VIEW_LIST.HOME:
                this.currentView = this.homeView;
                this.changeBackGround(ImageKey.LWHSG_lobby_bg);
                EventManager.SendMessage(GameEvent.CHANGE_GAME_STATE, new EventMessage(GAME_STATE.GAME_STOP));
                break;
            case VIEW_LIST.GAME:
                this.currentView = this.gameView;
                this.changeBackGround(ImageKey.game_bg);
                EventManager.SendMessage(GameEvent.CHANGE_GAME_STATE, new EventMessage(GAME_STATE.GAME_TIME, {time: GAME_TIME_STATE.OPEN}));
                break;
            default:
                console.error('no such view index: ', viewIndex);
                break;
        }
        this.currentView.onViewShow();
        this.handleResize();
    }

    public changeBackGround(bgName: string): void {
        this.bg.texture = Texture.from(bgName);
    }

    public getAudio(): void {
        if(PlayerPrefs.Get('lwhsg_music') === undefined) {
            PlayerPrefs.Set('lwhsg_music', true);
        }
    }

    public getGameView(): GameView {
        return this.gameView;
    }

    public getHomeView(): HomeView {
        return this.homeView;
    }

    public handleResize(): void {
        let width = ScreenManager.getWidth();
        let height = ScreenManager.getHeight();
        this.bg.position.set((ScreenManager.getWidth()  - MAX_SIZE.width + 100) / 2, (ScreenManager.getHeight() - MAX_SIZE.height) / 2);
        this.currentView.onResize(width, height);
    }

    public onVisibilityChanged(isVisible: boolean): void {
        this.currentView.onVisibilityChanged(isVisible);
    }

    public destroy(): void {
        ScreenManager.unSubscribe(this.handleResize);
        super.destroy({ children: true });
        Object.keys(this).forEach(key => this[key] = null);
    }
}