import State from './state';
import { GAME_TIME_STATE, GameEvent, GAME_STATE, SoundKey } from '../constants';
import { EventManager, EventMessage  } from '@base/tool/eventManager';
import Audio from '@base/tool/audio';
import gsap from 'gsap';

export default class GameTimeState extends State {
    private gsapDelayedCall: gsap.core.Tween;
    public enterState(data?: any): void {
        let time = data.time
        switch(time) {
            case GAME_TIME_STATE.OPEN:
                this.gameView.getStateViewPopModal().changeImage(GAME_TIME_STATE.OPEN);
                Audio.playSound(SoundKey.lwhsg_open_store);
                break;
            case GAME_TIME_STATE.REST:
                this.gameView.getStateViewPopModal().changeImage(GAME_TIME_STATE.REST);
                break;
            case GAME_TIME_STATE.CLOSE:
                this.gameView.getStateViewPopModal().changeImage(GAME_TIME_STATE.CLOSE);
                Audio.playSound(SoundKey.lwhsg_close_store);
                break;
            default:
                break;
        }
        this.openPopModal();
        this.gameView.resetNumber();
        this.gameView.getCountdown().reset();
    }

    public leaveState(): void {
        this.gameView.getStateViewPopModal().close();
        if(this.gsapDelayedCall) this.gsapDelayedCall.kill();
    }

    public clearAll(): void {

    }

    private openPopModal(): void {
        this.gameView.getStateViewPopModal().openWithAnim();
        this.gsapDelayedCall = gsap.delayedCall(2, ()=> {
            this.gameView.getStateViewPopModal().closeWithAnim();
            EventManager.SendMessage(GameEvent.CHANGE_GAME_STATE, new EventMessage(GAME_STATE.GAME_START));
        })
    } 
}