import { Sprite, Texture, Container } from 'pixi.js';
import { ImageKey, INGREDIENTS_TAG, SoundKey } from '../constants';
import Audio from '@base/tool/audio';
import gsap from 'gsap';

export default class Mixture extends Container {
    private pot: Sprite;
    private meat: Sprite;
    private vegetable: Sprite;
    private shrimp: Sprite;
    private egg: Sprite;
    private selectObject: Sprite;
    private wholeArray: Array<string>;
    constructor() {
        super();
        this.init();
    }

    public resetView(): void {
        this.pot.visible = false;
        this.meat.visible = false;
        this.vegetable.visible = false;
        this.shrimp.visible = false;
        this.egg.visible = false;
        this.selectObject.visible = false;
        this.wholeArray = [];
    }

    public playAni(tag: string, callBack: ()=> void): void {
        this.wholeArray.push(tag);
        let sprite = this.chooseAni(tag);
        this.visibleSelectObject(true);
        this.selectObject.texture = sprite.texture;
        let posX = tag === 'POT' ? 0 : 30;
        Audio.playSound(SoundKey.lwhsg_add_food);
        gsap.fromTo(this.selectObject, 
            {
                y: -100,
                x: posX
            },
            {
                duration: 0.6,
                y: -10,
                x: posX,
                alpha: 0,
                ease: 'none',
                onComplete:()=> {
                    this.visibleSelectObject(false);
                    sprite.visible = true;
                    callBack();
                    gsap.killTweensOf(this.selectObject);
                }
            });
    }

    public playＷholeObjectAni(data: Array<string>): void {
        for(let i = 0; i < data.length; i++) {
            let sprite = this.chooseAni(data[i]);
            sprite.visible = true;
        }
        gsap.fromTo(this, 
            { y: -100 },
            {
                duration: 0.6,
                y: 0,
                ease: 'none',
                onComplete:()=> {
                    gsap.killTweensOf(this);
                }
            });
    }

    public getWholeData(): Array<string> {
        return this.wholeArray;
    }

    private chooseAni(tag: string): Sprite {
        let object;
        switch(tag) {
            case INGREDIENTS_TAG[0]:
                object = this.pot;
                break;
            case INGREDIENTS_TAG[1]:
                object = this.meat;
                break;
            case INGREDIENTS_TAG[2]:
                object = this.vegetable;
                break;
            case INGREDIENTS_TAG[3]:
                object = this.shrimp;
                break;
            case INGREDIENTS_TAG[4]:
                object = this.egg;
                break;
            default:
                object = this.pot;
                break;
        }
        return object;
    }

    private init(): void {
        this.pot = new Sprite(Texture.from(ImageKey.pot_img));
        this.pot.visible = false;
        this.addChild(this.pot);
        this.vegetable = this.createSprite(ImageKey.vegetable_img);
        this.vegetable.position.set(35, 0);
        this.egg = this.createSprite(ImageKey.egg_img);
        this.egg.position.set(60, 10);
        this.shrimp = this.createSprite(ImageKey.shrimp_img);
        this.shrimp.position.set(20, 8);
        this.meat = this.createSprite(ImageKey.meat_img);
        this.meat.position.set(32, 20);
        this.selectObject = new Sprite(Texture.from(ImageKey.pot_img));
        this.selectObject.visible = false;
        this.addChild(this.selectObject);
        this.wholeArray = [];
    }

    private createSprite(texture: string): Sprite {
        let sprite = new Sprite(Texture.from(texture));
        sprite.visible = false;
        sprite.scale.set(0.7);
        this.addChild(sprite);
        return sprite;
    }

    private visibleSelectObject(bool: boolean): void {
        this.selectObject.visible = bool;
        this.selectObject.alpha = bool? 1 : 0;
    }
}