import { Container } from 'pixi.js';
import GlobalListenerAdapter from './common/globalListenerAdapter';
import GameController from './controller/gameController';
import RootView from './view/rootView';
import ColliderManager from '@base/collider/colliderManager';
import TimeManager from '@base/tool/timeManager';

// 遊戲最外層container, 回傳給大廳做初始化建置
export default class GameCanvas extends Container {
    private gameController: GameController;
    private rootView: RootView;

    private globalListenerAdapter: GlobalListenerAdapter;

    constructor() {
        super();
        this.onVisibilityChanged = this.onVisibilityChanged.bind(this);

        this.init();
    }

    private init() {
        this.gameController = new GameController();
        this.rootView = new RootView();
        this.addChild(this.rootView);

        // 若有使用到碰撞偵測再加上
        TimeManager.addGameListener(ColliderManager.Ins.Update);

        this.gameController.setRootView(this.rootView);
        this.gameController.initStateMachine();
    }

    public onVisibilityChanged(isVisible: boolean) {
        this.rootView.onVisibilityChanged(isVisible);
    }

    // 大廳調用將globalListener實體傳進來用於註冊window監聽事件
    public setGlobalListener(globalListener: any) {
        this.globalListenerAdapter = new GlobalListenerAdapter();
        this.globalListenerAdapter.setGlobalListener(globalListener);
        this.globalListenerAdapter.registerVisibleEvent(this.onVisibilityChanged);
    }

    public destroy() {
        super.destroy({ children: true });
    }
}