import { Sprite, Texture, IPoint, Text } from 'pixi.js';
import { MAX_SIZE, ImageKey, INGREDIENTS, INGREDIENTS_TAG, GameEvent, GAME_STATE, ANIMAL_ROUTE, SoundKey } from '../constants';
import BaseView from './baseView';
import Button from '@base/components/button';
import ScreenManager from '@base/tool/screenManager';
import DeskItmeCollider from '../components/collider/deskItmeCollider';
import StoveCollider from '../components/collider/stoveCollider';
import GuestTableCollider from '../components/collider/guestTableCollider';
import PausePopModal from '../components/popModal/pausePopModal';
import ResultPopModal from '../components/popModal/resultPopModal';
import Countdown from '../components/countdown';
import Animal from '../components/animal';
import GlobalGameData from '../model/globalGameData';
import StateViewPopModal from '../components/popModal/stateViewPopModal';
import { EventManager, EventMessage  } from '@base/tool/eventManager';
import Audio from '@base/tool/audio';
import PlayerPrefs from '@base/tool/playerPrefs';

enum Z_INDEX {
    SELECT_FRAME = 1,
    FIRE = 2,
    COLLIDER = 3,
    POPMODAL = 100,
};

// 遊戲承載層級
export default class GameView extends BaseView {
    private potBtn: Sprite;
    private meatBtn: Sprite;
    private vegetableBtn: Sprite;
    private shrimpBtn: Sprite;
    private eggBtn: Sprite;
    private fireBtn: Button;
    private selectFrame: Sprite;
    private clickSelectFrame: boolean = false;
    private ingredientsArray: Array<Sprite>;
    private deskItmeArray: Array<string>;
    private deskItmeCollider: DeskItmeCollider;
    private pauseBtn: Button;
    private pausePopModal: PausePopModal;
    private countdown: Countdown;
    private scoreText: Text;
    private scoreNumber: Text;
    private negativeText: Text;
    private negativeNumber: Text;
    private stateViewPopModal: StateViewPopModal;
    private animal: Animal;
    private resultPopModal: ResultPopModal;

    // 初始化
    public init(): void {
        this.ingredientsArray = [];
        this.deskItmeArray = [];
        this.topBar();
        this.initGuestView();
        this.initStoveItme();
        this.initDeskItme();
        this.initStateView();
        this.initanimal();
        this.pointerEventHandler();
        this.centerContainer.sortChildren();
        EventManager.AddListener(GameEvent.UPDATA_FRACTION, (msg: any) => { this.updataScoreNumber(msg) });
        EventManager.AddListener(GameEvent.UPDATA_NEGATIVE, (msg: any) => { this.updataNegativeNumber(msg) });
    }

    public onVisibilityChanged(isVisible: boolean): void {
        if (!isVisible ) {
            EventManager.SendMessage(GameEvent.CHANGE_GAME_STATE, new EventMessage(GAME_STATE.GAME_RESUME));
        }
    }

    public resetView(): void {
        EventManager.SendMessage(GameEvent.RESET_POT);
        EventManager.SendMessage(GameEvent.RESET_MENU);
        EventManager.SendMessage(GameEvent.RESET_STOVE_VIEW);
    }

    public getStateViewPopModal(): StateViewPopModal {
        return this.stateViewPopModal;
    }

    public getCountdown(): Countdown {
        return this.countdown;
    }

    public getAnimal(): Animal {
        return this.animal;
    }

    public getResultPopModal(): ResultPopModal{
        return this.resultPopModal;
    }

    public getPausePopModal(): PausePopModal{
        return this.pausePopModal;
    }

    public updataScoreNumber(msg: any): void {
        let number = Number(this.scoreNumber.text);
        number += msg.cMsg[0].fraction;
        this.scoreNumber.text = number.toString();
        GlobalGameData.Score = number;
        GlobalGameData.BestScore = number;
    }

    public updataNegativeNumber(msg: any): void  {
        let number = Number(this.negativeNumber.text);
        number += msg.cMsg[0].negative;
        this.negativeNumber.text = number.toString();
        if(number >= 100) {
            EventManager.SendMessage(GameEvent.CHANGE_GAME_STATE, new EventMessage(GAME_STATE.GAME_OVER));} 
        PlayerPrefs.Set('negativeNumber', number);
    }

    public resetNumber(): void {
        this.scoreNumber.text = '0';
        this.negativeNumber.text = '90';
        PlayerPrefs.Set('negativeNumber', 90);
        GlobalGameData.Score = 0;
    }

    private topBar(): void {
        this.pauseBtn = new Button(ImageKey.pause_btn);
        this.pauseBtn.position.set(MAX_SIZE.width / 2 * 0.3, MAX_SIZE.height / 2 * 0.15);
        this.pauseBtn.onTap = () => {
            EventManager.SendMessage(GameEvent.CHANGE_GAME_STATE, new EventMessage(GAME_STATE.GAME_RESUME));
            Audio.playSound(SoundKey.lwhsg_click_button);
        };
        this.pauseBtn.zIndex = Z_INDEX.FIRE;
        this.centerContainer.addChild(this.pauseBtn);
        this.pausePopModal = new PausePopModal();
        this.pausePopModal.zIndex = Z_INDEX.POPMODAL;
        this.centerContainer.addChild(this.pausePopModal);

        this.countdown = new Countdown();
        this.countdown.position.set(MAX_SIZE.width / 2 * 0.8, MAX_SIZE.height / 2 * 0.1);
        this.centerContainer.addChild(this.countdown);
        let style = {
            fontSize: 34, fill: 0x000000
        }
        this.scoreText = new Text("分數:", style);
        this.scoreText.position.set(MAX_SIZE.width / 2 * 0.5, MAX_SIZE.height / 2 * 0.1);
        this.centerContainer.addChild(this.scoreText);
        this.scoreNumber = new Text("0", style);
        this.scoreNumber.position.set(MAX_SIZE.width / 2 * 0.62, MAX_SIZE.height / 2 * 0.1);
        this.centerContainer.addChild(this.scoreNumber);

        this.negativeText = new Text("負評:", style);
        this.negativeText.position.set(MAX_SIZE.width / 2 * 1.4, MAX_SIZE.height / 2 * 0.1);
        this.centerContainer.addChild(this.negativeText);
        this.negativeNumber = new Text("90", style);
        this.negativeNumber.position.set(MAX_SIZE.width / 2 * 1.52, MAX_SIZE.height / 2 * 0.1);
        this.centerContainer.addChild(this.negativeNumber);

        this.resultPopModal = new ResultPopModal();
        this.resultPopModal.zIndex = Z_INDEX.POPMODAL;
        this.centerContainer.addChild(this.resultPopModal);
    }

    private initDeskItme(): void {
        this.potBtn = this.createSprite(ImageKey.pot_img, INGREDIENTS.POT);
        this.potBtn.position.set(MAX_SIZE.width / 2 * 0.55, MAX_SIZE.height * 0.84);

        this.meatBtn = this.createSprite(ImageKey.meat_img, INGREDIENTS.MEAT);
        this.meatBtn.position.set(MAX_SIZE.width / 2 * 0.72, MAX_SIZE.height * 0.87);

        this.vegetableBtn = this.createSprite(ImageKey.vegetable_img, INGREDIENTS.VEGETABLE);
        this.vegetableBtn.position.set(MAX_SIZE.width / 2 * 0.86, MAX_SIZE.height * 0.87);

        this.shrimpBtn = this.createSprite(ImageKey.shrimp_img, INGREDIENTS.SHRIMP);
        this.shrimpBtn.position.set(MAX_SIZE.width / 2, MAX_SIZE.height * 0.87);

        this.eggBtn = this.createSprite(ImageKey.egg_img, INGREDIENTS.EGG);
        this.eggBtn.position.set(MAX_SIZE.width / 2 * 1.14, MAX_SIZE.height * 0.87);

        this.fireBtn = new Button(ImageKey.fire_img);
        this.fireBtn.position.set(MAX_SIZE.width / 2 * 1.3, MAX_SIZE.height * 0.89);
        this.fireBtn.onTap = () => {
            this.fireHandler();
        };
        this.fireBtn.zIndex = Z_INDEX.FIRE;
        this.centerContainer.addChild(this.fireBtn);

        this.selectFrame = new Sprite(Texture.from(ImageKey.select_frame_img));
        this.selectFrame.position.set(MAX_SIZE.width / 2 * 1.297 - this.selectFrame.width / 2, MAX_SIZE.height * 0.909 - this.selectFrame.height / 2);
        this.selectFrame.zIndex = Z_INDEX.SELECT_FRAME;
        this.selectFrame.visible = false;
        this.centerContainer.addChild(this.selectFrame);

        this.deskItmeCollider = new DeskItmeCollider();
        this.deskItmeCollider.zIndex = Z_INDEX.COLLIDER;
        this.centerContainer.addChild(this.deskItmeCollider);
    }

    private initStoveItme(): void {
        for(let i = 0; i < 6; i++) {
            let stoveCollider = new StoveCollider();
            stoveCollider.zIndex = Z_INDEX.COLLIDER;
            stoveCollider.setCenterContainer(this.centerContainer);
            stoveCollider.position.set(MAX_SIZE.width / 2 * 0.62 + i * 101, MAX_SIZE.height / 2 * 1.49);
            this.centerContainer.addChild(stoveCollider);
        };
    }

    private initStateView(): void {
        this.stateViewPopModal = new StateViewPopModal();
        this.stateViewPopModal.zIndex = Z_INDEX.POPMODAL;
        this.centerContainer.addChild(this.stateViewPopModal);
    }

    private initGuestView(): void {
        for(let i = 0; i < 4; i++) {
            let guestTableCollider = new GuestTableCollider(i);
            switch(i) {
                case ANIMAL_ROUTE.ROUTE_1:
                    guestTableCollider.position.set(MAX_SIZE.width / 2 * 1.09, MAX_SIZE.height / 2 * 0.35);
                    break;
                case ANIMAL_ROUTE.ROUTE_2:
                    guestTableCollider.position.set(MAX_SIZE.width / 2 * 1.09, MAX_SIZE.height / 2 * 0.65);
                    break;
                case ANIMAL_ROUTE.ROUTE_3:
                    guestTableCollider.position.set(MAX_SIZE.width / 2 * 1.45, MAX_SIZE.height / 2 * 0.35);
                    break;
                case ANIMAL_ROUTE.ROUTE_4:
                    guestTableCollider.position.set(MAX_SIZE.width / 2 * 1.45, MAX_SIZE.height / 2 * 0.65);
            }
            this.centerContainer.addChild(guestTableCollider);
        }
    }

    private initanimal(): void {
        this.animal = new Animal();
        this.animal.position.set(MAX_SIZE.width / 2 * 0.31, MAX_SIZE.height / 2 * 0.29);
        this.centerContainer.addChild(this.animal);
    }

    private createSprite(texture: string, type: INGREDIENTS): Sprite {
        let sprite = new Sprite(Texture.from(texture));
        sprite.interactive = true;
        this.centerContainer.addChild(sprite);
        this.ingredientsArray.push(sprite);
        this.deskItmeArray.push(texture);
        return sprite;
    }

    private pointerEventHandler(): void {
        this.ingredientsArray.forEach((sprite, type: INGREDIENTS) => {
            sprite.on('pointerdown', (e) => {
                EventManager.SendMessage(GameEvent.STOVE_PLAYING, new EventMessage(false));
                this.pointerDownHandler(type);
                this.moveDeskItem(e.data.global);
            });
        });
        this.deskItmeCollider.on('pointermove', (e) => {
            this.moveDeskItem(e.data.global);
        });
        this.deskItmeCollider.on('pointerup', (e) => {
            this.visibleDeskItme(false);
            EventManager.SendMessage(GameEvent.STOVE_PLAYING, new EventMessage(true));
        });
    }

    private pointerDownHandler(type: INGREDIENTS): void {
            let pos = this.ingredientsArray[type].position;
            this.deskItmeCollider.texture = Texture.from(this.deskItmeArray[type]);
            this.deskItmeCollider.position.set(pos.x, pos.y);
            this.deskItmeCollider.setTag(INGREDIENTS_TAG[type]);
            this.visibleDeskItme(true);
    }

    private visibleDeskItme(bool: boolean): void {
        this.deskItmeCollider.interactive = bool;
        this.deskItmeCollider.alpha = bool? 1 : 0;
        this.deskItmeCollider.position.set(- MAX_SIZE.width, 0);
    }

    private fireHandler(): void {
        this.clickSelectFrame = !this.clickSelectFrame;
        this.selectFrame.visible = this.clickSelectFrame;
        this.ingredientsArray.forEach((sprite) => {
            sprite.buttonMode = !this.clickSelectFrame;
            sprite.interactive = !this.clickSelectFrame;
            sprite.alpha = this.clickSelectFrame ? 0.7 : 1;
        });
    }

    private moveDeskItem(pos: IPoint): void {
        let posX = pos.x / ScreenManager.gameRatio   - this.centerContainer.position.x - this.deskItmeCollider.width / 2;
        let posY = pos.y / ScreenManager.gameRatio - this.centerContainer.position.y - this.deskItmeCollider.height / 2;
        this.deskItmeCollider.position.set(posX, posY);
    }
}