import State from './state';
import { SoundKey } from '../constants';
import PlayerPrefs from '@base/tool/playerPrefs';
import GlobalGameData from '../model/globalGameData';
import Audio from '@base/tool/audio';

export default class GameOverState extends State {
    public enterState(data?: any): void {
        PlayerPrefs.Set("remainTime", null);
        this.gameView.getAnimal().resetView();
        this.gameView.resetView();
        this.gameView.getResultPopModal().openWithAnim();
        Audio.playSound(SoundKey.lwhsg_result);
        this.gameView.getResultPopModal().setScore(GlobalGameData.Score);
        this.gameView.getResultPopModal().setBestScore(GlobalGameData.BestScore);
    }

    public leaveState(): void {

    }

    public clearAll(): void {
        
    }
}