import { GAME_STATE } from '../constants';
import State from './state';
import IdleState from './idleState';
import RootView from '../view/rootView';
import GameStartState from './gameStartState';
import GameStopState from './gameStopState';
import GameResumeState from './gameResumeState';
import GameOverState from './gameOverState';
import GameTimeState from './gameTimeState';

export default class StateMachine {
    private stateMap: Map<GAME_STATE, State>;
    private currentState: State;

    public initState(rootView: RootView): void {
        let gameView = rootView.getGameView();
        this.stateMap = new Map();
        this.stateMap.set(GAME_STATE.IDLE, new IdleState(gameView, GAME_STATE.IDLE));
        this.stateMap.set(GAME_STATE.GAME_START, new GameStartState(gameView, GAME_STATE.GAME_START));
        this.stateMap.set(GAME_STATE.GAME_STOP, new GameStopState(gameView, GAME_STATE.GAME_STOP));
        this.stateMap.set(GAME_STATE.GAME_RESUME, new GameResumeState(gameView, GAME_STATE.GAME_RESUME));
        this.stateMap.set(GAME_STATE.GAME_OVER, new GameOverState(gameView, GAME_STATE.GAME_OVER));
        this.stateMap.set(GAME_STATE.GAME_TIME, new GameTimeState(gameView, GAME_STATE.GAME_TIME));
    }

    public changeState(state: GAME_STATE, data?: any): void {
        let nextState = this.stateMap.get(state);

        if (!nextState || nextState === this.currentState) {
            console.error('Wrong State: ', state);
            return;
        }

        if(this.currentState?.state === GAME_STATE.GAME_OVER && nextState.state === GAME_STATE.GAME_RESUME ) return;

        if (this.currentState) {
            this.currentState.leaveState();
        }

        this.currentState = nextState;
        this.currentState.enterState(data);
    }

    public getCurrentState(): GAME_STATE {
        return this.currentState.getState();
    }

    public destroy(): void {
        if (this.stateMap !== undefined) {
            this.stateMap.forEach(state => {
                state.clearAll();
            });
            this.stateMap.clear();
        }

        Object.keys(this).forEach(key => this[key] = null);
    }
}