import BaseModal from '@base/components/baseModal';
import { Sprite, Texture } from 'pixi.js';
import { ImageKey, SoundKey } from '../../constants';
import Button from '@base/components/button';
import Format from 'string-format';
import gsap from 'gsap';
import Audio from '@base/tool/audio';

enum PAGE {
    NEXT,
    PREV
}

export default class TutirialPopModal extends BaseModal {
    private bgSprite: Sprite;
    private view: Sprite;
    private pageNum: number = 1;
    private closeBtn: Button;
    private nextBtn: Button;
    private prevBtn: Button;
    private stopAni: boolean = false;

    constructor() {
        super()
        this.initContent();
    }

    public initContent(): void {
        this.bgSprite = new Sprite(Texture.from(ImageKey.tutorial_panel));
        this.bgSprite.position.set(- this.bgSprite.width / 2 , - this.bgSprite.height / 2);
        this.contentContainer.addChild(this.bgSprite);

        this.view = new Sprite( Texture.from(Format(ImageKey.tutorial_img, this.pageNum.toString())));
        this.view.position.set(-this.view.width / 2, -this.view.height /2);
        this.contentContainer.addChild(this.view);

        this.closeBtn = new Button(ImageKey.close_btn);
        this.closeBtn.onTap = () => {
            this.closeWithAnim();
            Audio.playSound(SoundKey.lwhsg_click_button);
        }
        this.closeBtn.position.set(this.bgSprite.width / 2 - this.closeBtn.width / 2, - this.bgSprite.height / 2 + this.closeBtn.height / 2);
        this.contentContainer.addChild(this.closeBtn);

        this.nextBtn = new Button(ImageKey.next_btn);
        this.nextBtn.position.set(this.bgSprite.width * 0.3, this.bgSprite.height * 0.4);
        this.nextBtn.onTap = () => {
            this.getNextIndex(PAGE.NEXT);
            this.playAni();
            Audio.playSound(SoundKey.lwhsg_click_button);
        }
        this.contentContainer.addChild(this.nextBtn);

        this.prevBtn = new Button(ImageKey.prev_btn);
        this.prevBtn.position.set(- this.bgSprite.width * 0.3, this.bgSprite.height * 0.4);
        this.prevBtn.onTap = () => {
            this.getNextIndex(PAGE.PREV);
            this.playAni();
            Audio.playSound(SoundKey.lwhsg_click_button);
        }
        this.contentContainer.addChild(this.prevBtn);
    }

    public reset(): void {
        this.pageNum = 1;
        this.view.texture = Texture.from(Format(ImageKey.tutorial_img, this.pageNum.toString()));
    }

    public getNextIndex(page: PAGE): void {
        this.stopAni = false;
        switch(page) {
            case PAGE.NEXT:
                if(this.pageNum === 5) {
                    this.stopAni = true;
                    break;
                }
                this.pageNum++;
                break;
            case PAGE.PREV:
                if(this.pageNum === 1) {
                    this.stopAni = true;
                    break;
                }
                this.pageNum--;
                break;
        }
    }

    public playAni(): void {
        if(this.stopAni) return;
        if(this.view) gsap.killTweensOf(this.view);
        gsap.to(this.view, {
            duration: 0.3,
            alpha: 0,
            onComplete: () => {
                this.view.texture = Texture.from(Format(ImageKey.tutorial_img, this.pageNum.toString()));
                this.view.alpha = 1;
                gsap.killTweensOf(this.view);
            }
        })
    }
}