import { Sprite, Texture, Container, AnimatedSprite } from 'pixi.js';
import { ImageKey, GameEvent, ANIMAL_ROUTE, ORDER, SoundKey } from '../constants';
import Format from 'string-format';
import Method from '@base/tool/method';
import gsap from 'gsap';
import { EventManager, EventMessage  } from '@base/tool/eventManager';
import Audio from '@base/tool/audio';

enum ANIMAL_STATE {
    NORMAL,
    GOOD,
    BAD
};

enum TIME_MANAGEMENT {
    START,
    STOP,
    PAUSE,
    RESUME
}

export default class Animal extends Container {
    private animalContainer: Container;
    private animalSprite: Sprite;
    private orderSprite: Sprite;
    private orderData: Array<string>;
    private animalAnimated: AnimatedSprite;
    private animalNumber: number;
    private moveData: number;
    private animalState: ANIMAL_STATE;
    private gsapAnimation: gsap.core.Timeline | null;
    private pauseAnimal: boolean = false;
    private isCheckFood: boolean = false;
    private reset: boolean = false;
    private timerId: any;
    private original: any;
    private remaining: any;

    constructor() {
        super()
        this.init();
        this.scale.set(0.7);

        EventManager.AddListener(GameEvent.SET_MENU, (msg: any) => { this.checkFood(msg) })
    }

    public get OrderData(): any { return this.orderData };

    public set OrderData(data: any) { this.orderData = Object.values(ORDER)[data-1] };

    public init(): void {
        this.animalContainer = new Container();
        this.addChild(this.animalContainer);
        this.animalSprite = new Sprite(Texture.from(Format(ImageKey.custom_img, '1')));
        this.animalSprite.visible = false;
        this.animalContainer.addChild(this.animalSprite);
        this.orderSprite = new Sprite(Texture.from(Format(ImageKey.order_img, '1')));
        this.orderSprite.scale.x = -1;
        this.orderSprite.anchor.set(1, 1);
        this.orderSprite.position.set(100, 10);
        this.orderSprite.visible = false;
        this.animalContainer.addChild(this.orderSprite);
        this.animalAnimated = new AnimatedSprite(this.getTextures(ImageKey.custom_good_img_1,ImageKey.custom_good_img_2, '1'));
        this.animalAnimated.animationSpeed = 0.1;
        this.animalAnimated.loop = true;
        this.animalAnimated.visible = false;
        this.animalAnimated.position.set(- 38, -16);
        this.animalContainer.addChild(this.animalAnimated);
        this.timerCallBack = this.timerCallBack.bind(this);
    }

    public timerCallBack(): void {
        if(this.isCheckFood) return;
        this.checkFood(null);
    }

    public resetView(): void {
        this.timeManagement(TIME_MANAGEMENT.STOP);
        this.animalContainer.position.set(0, 0);
        this.reset = true;
        this.isCheckFood = false;
        this.pauseAnimal = false;
        this.animalSprite.visible = false;
        this.orderSprite.visible = false;
        this.animalAnimated.loop = false;
        this.animalAnimated.stop();
        this.animalAnimated.visible = false;
        if(this.gsapAnimation) {
            this.gsapAnimation?.kill();
            this.gsapAnimation = null;
        }
    }

    public pauseAnimalAnimation(): void {
        this.timeManagement(TIME_MANAGEMENT.PAUSE);
        if(this.gsapAnimation) {
            this.gsapAnimation.pause();
        }
        if(this.animalAnimated) {
            this.animalAnimated.stop();
        }
    }

    public resumeAnimalAnimation(): void {
        this.timeManagement(TIME_MANAGEMENT.RESUME);
        if(this.gsapAnimation) {
            this.gsapAnimation.resume();
        }
        if(this.animalAnimated.visible) {
            this.animalAnimated.loop = true;
            this.animalAnimated.play();
        }
    }

    public getGsapAnimation(): gsap.core.Timeline | null {
        return this.gsapAnimation
    }

    public playAnimalAnimation(bool: boolean): void {
        this.isCheckFood = bool;
        this.reset = bool;
        this.timeManagement(TIME_MANAGEMENT.START);
        this.appearAnimalAnimation();
    }

    public checkFood(msg: any): void {
        let data = msg?.cMsg[0]? msg.cMsg[0]: msg;
        let equal = this.arrayEquals(data, this.OrderData);
        if(equal) {
            this.animalState = ANIMAL_STATE.GOOD;
            EventManager.SendMessage(GameEvent.UPDATA_FRACTION, new EventMessage({fraction: 12}));
            Audio.playSound(Format(SoundKey.lwhsg_good, Method.RandomIntRange(1, 2).toString()));
        } else {
            this.animalState = ANIMAL_STATE.BAD;
            EventManager.SendMessage(GameEvent.UPDATA_NEGATIVE, new EventMessage({negative: 2}));
            Audio.playSound(Format(SoundKey.lwhsg_bad, Method.RandomIntRange(1, 2).toString()));
        }
        this.isCheckFood = true;
        if(this.reset) return;
        this.playAnimalAnimated();
    }

    public getPauseAnimal(): boolean {
        return this.pauseAnimal;
    }

    public setPauseAnimal(pauseAnimal: boolean ){
        this.pauseAnimal = pauseAnimal;
    }

    private appearAnimalAnimation(): void {
        Audio.playSound(SoundKey.lwhsg_custom_enter);
        let random = Method.RandomIntRange(1, 4);
        this.animalNumber = random;
        this.animalSprite.texture = this.randomTexture(random, ImageKey.custom_img);
        this.animalSprite.visible = true;
        this.animalSprite.alpha = 1;
        this.gsapAnimation = gsap.timeline().fromTo(this.animalContainer, {
            y: 0
        }, {
            duration: 1,
            y: 150,
            ease: 'none',
            onComplete: () => {
                this.gsapAnimation?.kill();
                this.gsapAnimation = null;
                this.appearOrderAnimation();
            }
        })
    }

    private appearOrderAnimation(): void {
        let random = Method.RandomIntRange(1, 6);
        this.OrderData = random;
        this.orderSprite.texture = this.randomTexture(random, ImageKey.order_img);
        this.orderSprite.visible = true;
        this.gsapAnimation = gsap.timeline().fromTo(this.orderSprite.scale, {
            x: 0,
            y: 0
        }, {
            duration: 1,
            x: -1,
            y: 1,
            ease: 'none',
            onComplete: () => {
                this.gsapAnimation?.kill();
                this.gsapAnimation = null;
                this.moveAnimal();
            }
        })
    }

    private moveAnimal(): void {
        let random = Method.RandomIntRange(0, 3);
        this.moveData = random;
        let straight = 250;
        let right_road = 0;
        let up_road = 0;
        switch(random) {
            case ANIMAL_ROUTE.ROUTE_1:
                right_road = 670;
                up_road = 50;
                break;
            case ANIMAL_ROUTE.ROUTE_2:
                right_road = 670;
                up_road = 150;
                break;
            case ANIMAL_ROUTE.ROUTE_3:
                right_road = 1070;
                up_road = 50;
                break;
            case ANIMAL_ROUTE.ROUTE_4:
                right_road = 1070;
                up_road = 150;
                break;
        }

        this.gsapAnimation = gsap.timeline().to(this.animalContainer, {
            duration: 0.5,
            y: straight,
            ease: 'none',
        }).to(this.animalContainer, {
            duration: right_road / 800,
            x: right_road,
            ease: 'none'
        }).to(this.animalContainer, {
            duration: 20 / up_road,
            y: up_road,
            ease: 'none',
            onComplete: () => {
                EventManager.SendMessage(GameEvent.ANIMAL_DETAIL, new EventMessage({animalRoute: random}));
                this.gsapAnimation?.kill();
                this.gsapAnimation = null;
            }
        });
    }

    private leaveAnimal(): void {
        let straight = 250;
        let right_road = 0;
        let down_road = 0;
        switch(this.moveData) {
            case ANIMAL_ROUTE.ROUTE_1:
                right_road = 670;
                down_road = 50;
                break;
            case ANIMAL_ROUTE.ROUTE_2:
                right_road = 670;
                down_road = 150;
                break;
            case ANIMAL_ROUTE.ROUTE_3:
                right_road = 1070;
                down_road = 50;
                break;
            case ANIMAL_ROUTE.ROUTE_4:
                right_road = 1070;
                down_road = 150;
                break;
        }

        this.gsapAnimation = gsap.timeline().to(this.animalContainer, {
            duration: 150 / (straight + down_road),
            y: 250,
            ease: 'none',
        })
        .to(this.animalContainer, {
            duration: right_road / 800,
            x: 0,
            ease: 'none'
        })
        .to(this.animalContainer, {
            duration: 0.5,
            y: 0,
            ease: 'none',
        })
        .to(this.animalSprite, {
            duration: 1,
            alpha: 0,
            onComplete: () => {
                this.animalSprite.visible = false;
                this.gsapAnimation?.kill();
                this.gsapAnimation = null;
                this.playAnimalAnimation(false);
            }
        });
    }

    private randomTexture(random: number, texture: string): Texture {
        return Texture.from(Format(texture, `${random}`))
    }

    private arrayEquals(arrayA: Array<string>, arrayB: Array<string>): boolean {
        return Array.isArray(arrayA) &&
          Array.isArray(arrayB) &&
          arrayA.length === arrayB.length &&
          arrayA.every((val) => arrayB.indexOf(val) !== -1);
      }

    private getTextures(textureName1: string, textureName2: string, number: string): Array<Texture> {
        let texture1, texture2;
        let textures = [];
        texture1 = Texture.from(Format(`${textureName1}`, number));
        texture2 = Texture.from(Format(`${textureName2}`, number));
        textures.push(texture1, texture2);
        return textures;
    };

    private visibleNormal(bool: boolean): void {
        this.animalAnimated.loop = !bool;
        this.animalAnimated.visible = !bool;
        this.animalSprite.visible = bool;
        
    }

    private playAnimalAnimated(): void {
        switch(this.animalState) {
            case ANIMAL_STATE.GOOD:
                this.animalAnimated.textures = this.getTextures(ImageKey.custom_good_img_1,ImageKey.custom_good_img_2, `${this.animalNumber}`);
                break;
            case ANIMAL_STATE.BAD:
                this.animalAnimated.textures = this.getTextures(ImageKey.custom_bad_img_1,ImageKey.custom_bad_img_2, `${this.animalNumber}`);
                break;
        }

        this.visibleNormal(false);
        this.animalAnimated.loop = true;
        this.animalAnimated.visible = true;
        this.animalSprite.visible = false;
        this.orderSprite.visible = false;
        let sum = 0;
        this.animalAnimated.onLoop = () => {
            sum++;
            if(sum > 3) {
                this.animalAnimated.stop();
                this.visibleNormal(true);
                this.leaveAnimal();
                EventManager.SendMessage(GameEvent.RESET_POT);
            }
        }
        this.animalAnimated.play();
        EventManager.SendMessage(GameEvent.RESET_MENU);
    }

    private timeManagement(state: TIME_MANAGEMENT): void {
        
        if(this.timerId) clearTimeout(this.timerId);

        switch(state) {
            case TIME_MANAGEMENT.START:
                this.original = Date.now();
                this.remaining = 9000;
                this.timerId = setTimeout(() => {
                    this.timerCallBack();
                }, this.remaining);
                break;
            case TIME_MANAGEMENT.STOP:
                this.timerId = null;
                this.remaining = null;
                this.original = null;
                break;
            case TIME_MANAGEMENT.PAUSE:
                this.timerId = null;
                if(this.original) {
                    this.remaining -= Date.now() - this.original;
                }else {
                    this.remaining = 9000;
                }
                break;
            case TIME_MANAGEMENT.RESUME:
                this.original = Date.now();
                this.timerId = setTimeout(() => {
                    this.timerCallBack();
                }, this.remaining);
                break;
        }
    }
}