import State from './state';
import TimeManager from '@base/tool/timeManager';
import PlayerPrefs from '@base/tool/playerPrefs';
import { EventManager, EventMessage} from '@base/tool/eventManager';
import { GameEvent, GAME_STATE } from '../constants';

enum START_GAME_STATE {
    NONE,
    DELAY_TIME,
    ANIMAL_APPEAR_TIME,
    START_GAME_COUNTDOWN
};

const TOTOLE_TIME = 62;
const DELAY_TIME = 1;
const ANIMAL_APPEAR_TIME = 1;
const COUNTDOWN = 59;

const STATE1 = TOTOLE_TIME - DELAY_TIME;
const STATE2 = STATE1 - ANIMAL_APPEAR_TIME;
const STATE3 = STATE2 - COUNTDOWN;

export default class GameStartState extends State {
    private timeMonitorTimer: number = 0;
    private resultState: START_GAME_STATE | null;
    private remainTime: number;
    private initView: boolean = false;

    public enterState(data?: any): void {
        this.timeMonitorHandler = this.timeMonitorHandler.bind(this);
        this.remainTime = PlayerPrefs.Get("remainTime");
        this.gameView.resetNumber();
        this.gameView.getCountdown().reset();
        this.startTimeMonitorHandler();
        if(this.gameView.getAnimal().getPauseAnimal()) {
            this.gameView.getAnimal().resumeAnimalAnimation();
        }
    }

    public leaveState(): void {
        if(this.timeMonitorTimer > 0) {
            this.remainTime = this.timeMonitorTimer;
            PlayerPrefs.Set("remainTime", this.remainTime);
        }
        this.stopTimeMonitorHandler();
        this.resultState = START_GAME_STATE.NONE;
    }

    public clearAll(): void {

    }

    public timeMonitorHandler(time: number): void {
        this.timeMonitorTimer -= time / 60;
        if(this.timeMonitorTimer < 0) {
            this.stopTimeMonitorHandler();
            EventManager.SendMessage(GameEvent.CHANGE_GAME_STATE, new EventMessage(GAME_STATE.GAME_OVER));
            return;
        };
        this.syncStage();
        this.gameView.getCountdown().playCountdownAni(this.timeMonitorTimer);
    }

    private startTimeMonitorHandler(): void {
        if(this.remainTime && this.remainTime > 0) {
            this.timeMonitorTimer = this.remainTime;
        } else {
            this.timeMonitorTimer = TOTOLE_TIME;
        }
        TimeManager.addGameListener(this.timeMonitorHandler);
    }

    private stopTimeMonitorHandler(): void {
        TimeManager.removeGameHandler(this.timeMonitorHandler);
        this.timeMonitorTimer = 0;
        this.gameView.getCountdown().setplayAni(false);
    }

    private syncStage(): void {
        if(this.timeMonitorTimer > STATE1) {
            if(this.resultState === START_GAME_STATE.DELAY_TIME) return;
            this.resultState = START_GAME_STATE.DELAY_TIME;
            return;
        } else if(this.timeMonitorTimer > STATE2) {
            if(this.resultState === START_GAME_STATE.ANIMAL_APPEAR_TIME) return;
            this.resultState = START_GAME_STATE.ANIMAL_APPEAR_TIME;
            if(!this.gameView.getAnimal().getPauseAnimal() || !this.initView) {
                this.gameView.getAnimal().playAnimalAnimation(false);
                this.initView = true;
            }
            return;
        } else if(this.timeMonitorTimer > STATE3) {
            if(this.resultState === START_GAME_STATE.START_GAME_COUNTDOWN) return;
            this.resultState = START_GAME_STATE.START_GAME_COUNTDOWN;
            this.gameView.getStateViewPopModal().closeWithAnim();
            this.gameView.getCountdown().setplayAni(true);
            return;
        }
    }
}