import { Sprite, Texture, Graphics } from 'pixi.js';
import { ImageKey, MAX_SIZE, VIEW_LIST, GameEvent, SoundKey } from '../constants';
import BaseView from './baseView';
import Button from '@base/components/button';
import TutirialPopModal from '../components/popModal/tutirialPopModal';
import RankPopModal from '../components/popModal/rankPopModal';
import { EventManager, EventMessage  } from '@base/tool/eventManager';
import gsap from 'gsap';
import Audio from '@base/tool/audio';
import PlayerPrefs from '@base/tool/playerPrefs';

enum Z_INDEX {
    POP_MODAL = 100,
    MASK = 101,
}

// 遊戲承載層級
export default class HomeView extends BaseView {
    private logo: Sprite;
    private play_btn: Button;
    private tutirial_btn: Button;
    private audio_btn: Button;
    private rank_btn: Button;
    private tutirialPopModal: TutirialPopModal;
    private rankPopModal: RankPopModal;
    private playAni: gsap.core.Animation;
    private transfer_heart: Sprite;
    private maskGraphics: Graphics;

    // 初始化
    public init(): void {
        this.logo = new Sprite(Texture.from(ImageKey.logo));
        this.logo.position.set(this.logo.width / 2, 0)
        this.centerContainer.addChild(this.logo);

        this.play_btn = new Button(ImageKey.play_btn);
        this.play_btn.position.set(MAX_SIZE.width / 2, MAX_SIZE.height * 0.8);
        this.play_btn.onTap = () => {
            Audio.playSound(SoundKey.lwhsg_click_button);
            this.enterGaemAni();
        };
        this.centerContainer.addChild(this.play_btn);

        this.tutirial_btn = new Button(ImageKey.tutorial_btn);
        this.tutirial_btn.position.set(MAX_SIZE.width * 0.75, MAX_SIZE.height * 0.1);
        this.tutirial_btn.onTap = () => {
            Audio.playSound(SoundKey.lwhsg_click_button);
            this.tutirialPopModal.openWithAnim();
        };
        this.centerContainer.addChild(this.tutirial_btn);

        this.tutirialPopModal = new TutirialPopModal();
        this.tutirialPopModal.zIndex = Z_INDEX.POP_MODAL;
        this.centerContainer.addChild(this.tutirialPopModal);
        
        this.audio_btn = new Button(ImageKey.audio_on_btn);
        this.audio_btn.position.set(MAX_SIZE.width * 0.84, MAX_SIZE.height * 0.1);
        this.audio_btn.zIndex = 2;
        this.audio_btn.onTap = () => {
            Audio.playSound(SoundKey.lwhsg_click_button);
            let clickAudio = PlayerPrefs.Get("lwhsg_music");
            this.changeAudio(clickAudio);
        };
        this.centerContainer.addChild(this.audio_btn);

        this.rank_btn = new Button(ImageKey.rank_btn);
        this.rank_btn.position.set(MAX_SIZE.width * 0.2, MAX_SIZE.height * 0.1);
        this.rank_btn.onTap = () => {
            Audio.playSound(SoundKey.lwhsg_click_button);
            this.rankPopModal.openWithAnim();
        };
        this.centerContainer.addChild(this.rank_btn);

        this.rankPopModal = new RankPopModal();
        this.rankPopModal.zIndex = Z_INDEX.POP_MODAL;
        this.centerContainer.addChild(this.rankPopModal);

        this.maskGraphics = new Graphics();
        this.maskGraphics.zIndex = Z_INDEX.MASK;
        this.maskGraphics.beginFill(0xFFFFFF);
        this.maskGraphics.drawRect(0, 0, MAX_SIZE.width * 2, MAX_SIZE.height * 2);
        this.maskGraphics.endFill();
        this.maskGraphics.interactive = true;
        this.maskGraphics.visible = false;
        this.centerContainer.addChild(this.maskGraphics);

        this.transfer_heart = new Sprite(Texture.from(ImageKey.transfer_heart));
        this.transfer_heart.position.set(MAX_SIZE.width * 0.2, 0);
        this.transfer_heart.visible = false;
        this.centerContainer.addChild(this.transfer_heart);

        this.centerContainer.sortChildren();
        this.playBtnAni();
        this.initAudio();
    }

    public onVisibilityChanged(isVisible: boolean): void {
        if (isVisible) {
            this.playBtnAni();
            this.initAudio();
        }
        else {
            gsap.killTweensOf(this.playAni);
        }
    }

    public resetView(): void {
        this.transfer_heart.visible = false;
    }

    private initAudio(): void {
        if(PlayerPrefs.Get('lwhsg_music')) {
            this.audio_btn.texture = Texture.from(ImageKey.audio_on_btn);
            Audio.playMusic(SoundKey.lwhsg_game_bgm);
            Audio.setVolume(1);
        } else {
            this.audio_btn.texture = Texture.from(ImageKey.audio_off_btn);
            Audio.stopMusic();
            Audio.setVolume(0);
        }
    }

    private playBtnAni(): void {
        this.playAni = gsap.fromTo(this.play_btn.scale, {
            x: 1.1,
            y: 1.1
        }, {
            duration: 1,
            x: 0.9,
            y: 0.9,
            repeat: -1,
            ease: "none",
            yoyo: true
        })
    }

    private enterGaemAni(): void {
        // duration: 1.5
        this.transfer_heart.visible = true;
        this.maskGraphics.visible = true;
        gsap.fromTo(this.transfer_heart.scale,
            { x: 0, y: 0 },{ x: 1, y: 1, duration: 0.5, ease: "none" });
        gsap.fromTo(this.maskGraphics,
            { alpha: 0 },{ alpha: 0.8, duration: 0.5, ease: "none", onComplete: () => {
                this.transfer_heart.visible = false;
                this.maskGraphics.visible = false;
                gsap.killTweensOf(this.maskGraphics);
                gsap.killTweensOf(this.transfer_heart.scale);
                EventManager.SendMessage(GameEvent.CHANGE_VIEW, new EventMessage(VIEW_LIST.GAME));
            } });
    }

    private changeAudio(clickAudio: boolean): void {
        if(clickAudio) {
            this.audio_btn.texture = Texture.from(ImageKey.audio_off_btn);
            PlayerPrefs.Set('lwhsg_music', false);
            Audio.stopMusic();
            Audio.setVolume(0);
        } else {
            this.audio_btn.texture = Texture.from(ImageKey.audio_on_btn);
            PlayerPrefs.Set('lwhsg_music', true);
            Audio.playMusic(SoundKey.lwhsg_game_bgm);
            Audio.setVolume(1);
        }
    }
}